.project-article__text {
  font-weight: 300;
  font-style: normal;
  font-size: 20px;
  font-family: 'Formular', 'Arial', sans-serif;
  letter-spacing: -0.01em;
  line-height: 1.4;
  margin: 0;
  max-width: 602px;
}

.project-article__text:last-child {
  margin: 0;
}

.project-article__text:not(:first-child) {
  text-indent: 60px;
}


@media (max-width: 860px) {
  .project-article__text {
    max-width: 100%;
  }
}