.link_type_underline {
  background: transparent linear-gradient(var(--content-color), var(--content-color)) no-repeat 0 100%;
  background-size: 0% 1px;
  padding-bottom: 2px;
  transition: background-size 0.4s;
  width: fit-content;
}

.link_type_underline:hover {
  background-size: 100% 1px;
}